import { TitleCasePipe } from './pipes/title-case.pipe';
import { ArticleService } from './services/article.service';
import { ForumComponent } from './forum/forum.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { MatInputModule } from '@angular/material/input';

import { AppComponent } from './app.component';
import { LocationStrategy, HashLocationStrategy} from '@angular/common';
import { BsNavbarComponent } from './bs-navbar/bs-navbar.component';
import { HomeComponent } from './home/home.component';
import { ForumsPortalComponent } from './forums-portal/forums-portal.component';
import { ForumService } from './services/forum.service';
import { ForumListComponent } from './forum-list/forum-list.component';
import { ForumCategoriesComponent } from './forum-categories/forum-categories.component';
import { ZippyComponent } from './zippy/zippy.component';
import { ForumArticleComponent } from './forum-article/forum-article.component';
import { CommentListComponent } from './comment-list/comment-list.component';
import { CommentService } from './services/comment.service';

const routes = [
  { path: "about", component: AboutUsComponent },
  { path: "forums/:id", component: ForumComponent },
  { path: "article/:id", component: ForumArticleComponent },
  { path: "forums", component: ForumsPortalComponent },
  { path: "", component: HomeComponent }
]

@NgModule({
  declarations: [
    AppComponent,
    BsNavbarComponent,
    HomeComponent,
    AboutUsComponent,
    ForumComponent,
    ForumsPortalComponent,
    ForumListComponent,
    TitleCasePipe,
    ForumCategoriesComponent,
    ZippyComponent,
    ForumArticleComponent,
    CommentListComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    MatInputModule,
    RouterModule.forRoot(routes)
  ],
  providers: [
    { provide: LocationStrategy, useClass: HashLocationStrategy },
    ArticleService, ForumService, CommentService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



