import { QueryInfo } from './../models/QueryInfo';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Forum } from '../models/Forum';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class ForumService {
  private url: string = "http://localhost:8080/forums";

  constructor(private http: HttpClient) { }

  private setParams(info: QueryInfo){
    let Params = new HttpParams();
    Params = Params.append('page', info.page + '');
    Params = Params.append('size', info.size + '');

    return Params;
  }

  get(id: number) {

  }

  getAll(queryInfo: QueryInfo): Observable<Forum[]> {
   let Params = this.setParams(queryInfo);

    return this.http.get<Forum[]>(this.url, {params : Params});
  }
  getCategories(){
    return this.http.get(this.url+"/categories");
  }

getByCategory(category){
  return this.http.get<Forum[]>(this.url+"/"+category);
}
  create(article: Forum) {

  }
  update(article: Forum) {

  }
  delete(id: number) {

  }
}



